import { Router } from 'express';
import { checkSchema } from 'express-validator';
import {
  createTokenPair,
  updateTokenPair,
  deleteTokenPair,
  getTokenPairs,
  getTokenPairBySymbol,
  getTokenPairById,
} from '../controllers';
import { tokenPairsValidationSchema } from '../utils';
import { authorize, validationMiddleware } from '../middlewares';

const router = Router();

router.post(
  '/',
  authorize,
  checkSchema(tokenPairsValidationSchema),
  validationMiddleware,
  createTokenPair,
);
router.get('/', validationMiddleware, getTokenPairs);
router.get('/by-symbol/:symbol', validationMiddleware, getTokenPairBySymbol);
router.get('/:id', validationMiddleware, getTokenPairById);
router.put(
  '/:id',
  authorize,
  checkSchema(tokenPairsValidationSchema),
  validationMiddleware,
  updateTokenPair,
);
router.delete('/:id', authorize, validationMiddleware, deleteTokenPair);

export default router;
